import React from 'react';
import { List, InputItem } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { USER_SHOW } from '@/helpers/APIHelper';
import { useQuery } from '@apollo/client';
import { Page, PageTitle } from '../../Login/styles';

const { Item } = List;

const Safe: React.FC = () => {
  const { data = {} } = useQuery(USER_SHOW);
  const history = useHistory();
  const handleChangePwd = (): void => {
    history.push('/change-pwd');
  };
  const { userName, email } = data?.getUserInfo || {};
  return (
    <Page>
      <PageTitle>账号与安全</PageTitle>
      <List>
        <InputItem disabled value={email}>
          邮箱：
        </InputItem>
        <InputItem disabled value={userName}>
          用户名：
        </InputItem>
        <Item arrow="horizontal" onClick={handleChangePwd}>
          更改密码
        </Item>
      </List>
    </Page>
  );
};

export default Safe;
